const React = require("react");

const LoginButton = require("../auth/login-button")
const CurrentUserStore = require("../../stores/current-user-store");

const Home = React.createClass({displayName: "Home",
    mixins: [CurrentUserStore.mixin],
    render:function(){
        var auth = [
            (React.createElement(LoginButton, {authProvider: "Facebook", className: "m1"})),
            (React.createElement(LoginButton, {authProvider: "Twitter", className: "m1"})),
            (React.createElement(LoginButton, {authProvider: "Google", className: "m1"}))
        ];
        if(this.state.authState === "progress") {
            auth = (React.createElement("img", {src: "/img/loading_bar.gif"}))
        }
        if(this.state.authState === "done") {
            auth = (React.createElement("a", null, "Get started"))
        }

        return (
            React.createElement("div", {className: "bg-white rounded p1"}, 
                React.createElement("h1", {className: "center"}, "Podato"), 
                React.createElement("h2", {className: "center"}, "Enjoy Podcasts Together"), 
                React.createElement("p", {className: "center"}, 
                    auth
                )
            )
        );
    },
    getInitialState:function(){
        return {authState: null};
    },
    storeDidChange:function(){
        var authState = null;
        if(CurrentUserStore.getCurrentUser() == null){
            if (CurrentUserStore.getLoggingIn()) {
                authState = "progress";
            }
        }else{
            authState = "done";
        }
        this.setState({authState:authState});
    }
});

module.exports = Home
