const React = require("react");

const AuthActions = require("../../actions/auth-actions");

const LoginButton = React.createClass({displayName: "LoginButton",
    render:function(){
        var className = "button not-rounded " + (this.props.className || "");
        return (
            React.createElement("a", {className: className, onClick: this.login}, "Sign in with ", this.props.authProvider)
        )
    },
    login:function(e){
        e.preventDefault();
        AuthActions.login(this.props.authProvider.toLowerCase());
    }
});

module.exports = LoginButton;
